.bottom-btn {
  padding-bottom: 0.4rem;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  z-index: 10;
}
.bottom-btn .btn {
  width: 3.43rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  line-height: 0.4rem;
  color: #ffffff;
  font-size: 0.16rem;
  margin: 0 auto;
}
