.edit .input-item {
  width: 3.75rem;
  height: 0.45rem;
  background-color: #ffffff;
  box-shadow: inset 0rem -0.01rem 0rem 0rem #eeeeee;
}
.edit .input-item .am-input-label {
  width: 0.55rem;
  text-align: right;
  line-height: 0.255rem;
  color: #000000;
  font-size: 0.15rem;
  margin-right: 0.6rem;
}
.edit .input-item .relation {
  float: left;
  width: 0.55rem;
  text-align: right;
  line-height: 0.255rem;
  color: #000000;
  font-size: 0.15rem;
  margin-right: 0.6rem;
  line-height: 0.45rem;
}
.edit .input-item select {
  float: left;
  border: none;
  width: 2.55rem;
  padding: 2px 0;
  border: 0;
  background-color: transparent;
  line-height: 1;
  box-sizing: border-box;
  height: 0.45rem;
  color: #999999 !important;
  font-size: 0.15rem;
}
.edit ::-webkit-input-placeholder {
  line-height: 0.255rem;
  color: #999999;
  font-size: 0.15rem;
  text-align: left;
}
.edit ::-moz-placeholder {
  line-height: 0.255rem;
  color: #999999;
  font-size: 0.15rem;
  text-align: left;
}
.edit :-ms-input-placeholder {
  line-height: 0.255rem;
  color: #999999;
  font-size: 0.15rem;
  text-align: left;
}
.edit ::-ms-input-placeholder {
  line-height: 0.255rem;
  color: #999999;
  font-size: 0.15rem;
  text-align: left;
}
.edit ::placeholder {
  line-height: 0.255rem;
  color: #999999;
  font-size: 0.15rem;
  text-align: left;
}
.edit .edit-sign {
  position: fixed !important;
  bottom: 0;
  height: 0.7rem;
  background: #fff;
  z-index: 15;
  width: 100%;
}
.edit .edit-sign .edit-sign-btn {
  margin-left: 0.15rem;
  width: 3.43rem;
  height: 0.4rem;
  line-height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  font-size: 0.16rem;
}
.edit .am-list-item.am-input-item {
  padding: 0;
}
.edit .am-list-item.am-input-item .am-input-control input {
  padding: 2px 4px;
}
